:root {
  /* color */
  --color_main: #a3a2e5;
}

* {
  margin: 0;
  padding: 0;
  color: inherit;
  line-height: 1;
  text-align: center;
}

body {
  color: #222;
  padding-top: 30px;
}

h1 {
  font-size: 25px;
}

h2 {
  padding: 20px;
  font-size: 20px;
}

button {
  border: 0;
  background: none;
}

.quiz_wrap {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 400px;
  height: 400px;
  margin: auto;
  padding: 50px;
  border-radius: 15px;
  box-sizing: border-box;
  box-shadow: 3px 8px 20px rgba(0, 0, 0, 0.3);
  background: #fff;
}

.quiz_wrap_bg {
  border: 0;
  background: var(--color_main);
}

.quiz_wrap_bg h2 {
  color: #fff;
}

.question h1 {
  margin-bottom: 15px;
}

.question h2 {
  word-break: keep-all;
  line-height: 1.5;
}

.OXButton {
  display: flex;
  gap: 10px;
  justify-content: center;
}

.OXButton button {
  padding: 30px 40px;
  border-radius: 5px;
  box-sizing: border-box;
  background-color: var(--color_main);
  color: #fff;
  font-size: 30px;
  cursor: pointer;
}

.answer {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

.answer h2 {
  margin-top: 80px;
  line-height: 1.5;
}

.answer button,
.answer span {
  display: flex;
  gap: 8px;
  color: #fff;
  font-size: 14px;
  font-weight: bold;
  cursor: pointer;
}

.answer .svg-inline--fa.fa-angle-right path {
  color: #fff;
}

.score {
  height: 100%;
  display: flex;
  gap: 30px;
  flex-direction: column;
  color: #fff;
}